<template>
	<view class="box">
		<view class="status-bar"></view>

		<view class="box-content">

			<!-- <button type="primary" plain="true" @click="isSupport()">是否支持蓝牙</button> -->
			<!-- <button type="primary" plain="true" @click="isOpen()">蓝牙是否打开</button> -->
			<!-- <button type="primary" plain="true" @click="openBT()">打开蓝牙</button> -->
			<!-- <button type="primary" plain="true" @click="closeBT()">关闭蓝牙</button> -->
			<!-- <button type="primary" plain="true" @click="bluetoothList()">搜索蓝牙列表</button> -->
			<!-- <button type="primary" plain="true" @click="stopSearch()">停止搜索蓝牙</button> -->
			<!-- <button type="primary" plain="true" @click="listBondedBT()">已经配对列表</button> -->
			<!-- <button type="primary" plain="true" @click="bluetoothConnect()">连接蓝牙</button> -->
			<!-- <button type="primary" plain="true" @click="bluetoothConnectState()">判断蓝牙是否连接</button> -->
			<!-- <button type="primary" plain="true" @click="printText()">打印测试</button> -->
			<!-- <button type="primary" plain="true" @click="breakConnect()">断开连接</button> -->


			<!-- 选择界面 -->
			<u-popup :show="popupSelect" mode="center" class="u-popup-center" :closeable="true" @close="popupSelect=false">
				<view class="popup-s-v">
					<view @tap="listBondedBT">连接已配对设备</view>
					<view @tap="bluetoothList">搜索未配对设备</view>
				</view>
			</u-popup>
			<!-- 蓝牙界面 -->
			<u-popup :show="popupShow" mode="center" class="u-popup-center" :closeable="true" @close="popupShow=false">
				<scroll-view style="max-height: 70vh;" scroll-y="true">
					<view class="list" v-for="(v, i) in list" @tap="radioChange(v.btName+'-'+v.btAddress)">
						<text v-html="v.btName">{{v.btName}}</text>-
						<text v-html="v.btAddress">{{v.btAddress}}</text>
					</view>
				</scroll-view>
			</u-popup>
			
			<u-toast ref="uToast"></u-toast>

		</view>
	</view>
</template>

<script>
	// const plug = uni.requireNativePlugin('Html5app-Gprinter');
	const print = uni.requireNativePlugin('Common-Print');
	export default {
		data() {
			return {
				popupShow:false,
				list: [],
				popupSelect:false,

				path: "! 0 200 200 400 1\r\n" +
					"B 128 2 2 64 0 10 3100488650709\r\n" +
					"TEXT 24 0 0 80 3100488650709\r\n" +

					"BARCODE QR 0 110 M 2 U 7\r\n" +
					"MA,www.html5-app.com\r\n" +
					"ENDQR\r\n" +

					"PRINT\r\n",
			}
		},
		mounted() {

		},
		methods: {
			// 连接蓝牙
			radioChange(item){
				let _this=this
				print.bluetoothConnect({
					btAddress: item.split("-")[1],
					btName: item.split("-")[0]
				}, result => {
					console.log("连接蓝牙",result)
					if(result.code==true){
						let obj = {
							btAddress: item.split("-")[1],
							btName: item.split("-")[0]
						}
						uni.setStorageSync('printDevice', obj);
						_this.$refs.uToast.show({
							type:"success",
							message: "连接设备成功"
						})
						_this.popupShow=false
					}
				});
			},
			// 搜索已配对蓝牙
			listBondedBT() {
				let _this=this
				print.listBondedBT(result => {
					console.log('已配对列表',result);
					let res=JSON.parse(result.list)
					_this.popupSelect=false
					_this.list=res
					_this.popupShow=true
				});
			},
			// 搜索未配对蓝牙设备
			bluetoothList() {
				let _this=this
				uni.showLoading({
					title: "搜索中"
				});
				print.bluetoothList({}, result => {
					console.log("搜索未配对蓝牙设备",result)
					if(result.code==0){
						uni.hideLoading()
						_this.$refs.uToast.show({
							type:"success",
							message: "搜索完成"
						})
						_this.popupSelect=false
						_this.list=result.list
						_this.popupShow=true
					}else if(result.code==2){
						
					}else{
						uni.hideLoading()
					}
				});
			},
			// 判断蓝牙是否打开
			isOpen() {
				let _this = this
				print.isOpen(result => {
					// console.log("判断蓝牙是否打开",result)
					if(result.status){
						_this.bluetoothConnectState()
					}else{
						print.openBT(result => {
							console.log("开启蓝牙是否成功",result)
						});
						return
					}
				});
			},
			// 打印数据
			printText() {
				let _this = this
				print.bluetoothPrint({
					content: _this.path
				}, result => {
					console.log("打印数据",result)
					if(result.code==-200){
						_this.isOpen()
					}else{
						
					}
				});
			},
			//判断蓝牙与打印机是否连接
			bluetoothConnectState() {
				let _this=this
				uni.getStorage({
					key: 'printDevice',
					success: (res)=> {
						print.bluetoothConnectState({
							btAddress: res.data.btAddress
						}, result => {
							console.log("判断蓝牙与打印机是否连接",result)
							if(result.state){
								
							}else{
								_this.popupSelect=true
								return
							}
						});
					},
					fail: (err)=> {
						_this.popupSelect=true
						return
					}
				});
			},
			
			
			
			
			
			
			
			
			
			
			
			isSupport() {
				print.isSupport(result => {
					//result数据：{"status":true} 
					const msg = JSON.stringify(result);
					console.log(msg);
					console.log('是否支持蓝牙：' + result.status);

				});
			},
			
			closeBT() {
				print.closeBT(result => {
					//result数据：{"status":true} 
					const msg = JSON.stringify(result);
					console.log(msg);
					console.log('蓝牙是否关闭：' + result.status);

				});
			},
			
			stopSearch() {
				print.stopSearch(result => {
					//result数据：{"status":true} 
					const msg = JSON.stringify(result);
					console.log(msg);

				});
			},
			breakConnect() {
				print.breakConnect(result => {
					//result数据：{"status":true} 
					const msg = JSON.stringify(result);
					console.log(msg);

				});
			}
			
			
		},
	}
</script>

<style lang="scss" scoped>
	.box-content>* {
		margin-top: 10px;
	}

	/deep/.u-popup {
		max-height: 60vh;

		.u-popup__content {
			padding: 15px 10px 20px;
			.popup-s-v{
				display: flex;
				justify-content: space-between;
			}
			.popup-s-v>uni-view{
				width: calc(50% - 21px);
				padding:40px 10px 50px;
				text-align: center;
				font-size: 24px;
				word-break: break-all;
				
			}
			.popup-s-v>uni-view:first-child{
				border-right: 1px solid gray;
			}
		}

		.uni-scroll-view-content {
			box-sizing: border-box;

			.list {
				padding: 8px;
				border-bottom: 1px solid #EAEBEC;
			}

			.list:nth-child(even) {
				background-color: #FAFAFA;
			}
		}
	}
</style>
